import { useState } from "react";
import { useNavigate } from "react-router-dom";
import useInput from "../../hooks/common/useInput";
import useSendEmail from "../../hooks/query/useSendEmail";
import { useRegisterStore } from "../../store/useRegisterStore";
import validateEmail from "../../utils/validateEmail";
import DefaultButton from "../atoms/Button/DefaultButton";
import LinkText from "../atoms/Text/LinkText";
import Text from "../atoms/Text/Text";
import AuthForm from "../molecules/Form/AuthForm";
import AuthHeader from "../molecules/Text/AuthHeader";

const RegisterStep1 = () => {
  const { setStep, setEmail, setName, setPassword } = useRegisterStore();
  const navigate = useNavigate();
  const [email, changeEmail] = useInput();
  const [name, changeName] = useInput();
  const [password, changePassword] = useInput();
  const [errorMessage, setErrorMessage] = useState("");

  const { mutate: sendEmail } = useSendEmail();

  const onLoadLogin = () => navigate("/login");

  const onRegister = () => {
    if (!email || !name || !password) {
      return setErrorMessage("  .");
    }
    if (!validateEmail(email) || password.length < 8) {
      return setErrorMessage("    .");
    }
    setErrorMessage("");
    setEmail(email);
    setName(name);
    setPassword(password);
    sendEmail({ email, name, password });
    setStep(2);
  };

  return (
    <>
      <AuthHeader text=" " />{" "}
      {errorMessage !== "" && (
        <Text
          text={errorMessage}
          color="invite-danger"
          fontSize="xs"
          fontWeight="bold"
          mb={8}
        />
      )}
      <AuthForm
        text=""
        type="email"
        value={email}
        onChange={changeEmail}
      />
      <AuthForm text="" value={name} onChange={changeName} />
      <AuthForm
        text=""
        type="password"
        value={password}
        onChange={changePassword}
      />
      <DefaultButton text="" onClick={onRegister} height={44} mb={12} />
      <LinkText text="  ?" onClick={onLoadLogin} />
    </>
  );
};

export default RegisterStep1;
