import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import DefaultButton from "../components/atoms/Button/DefaultButton";
import LinkText from "../components/atoms/Text/LinkText";
import Text from "../components/atoms/Text/Text";
import DefaultModal from "../components/organisms/AuthModal";
import useInput from "../hooks/common/useInput";
import HeaderHelmet from "../components/atoms/Helmet";
import AuthDesc from "../components/molecules/Text/AuthDesc";
import LoginForm from "../components/molecules/Form/LoginForm";
import useLogin from "../hooks/query/useLogin";
import { useState } from "react";
import validateEmail from "../utils/validateEmail";

const Login = () => {
  const navigate = useNavigate();
  const [email, changeEmail] = useInput();
  const [password, changePassword] = useInput();
  const [errorMessage, setErrorMessage] = useState("");

  const { mutate: login } = useLogin(email);

  const onLoadRegister = () => navigate("/register");

  const onLogin = () => {
    if (!email || !password) {
      return setErrorMessage("  .");
    }
    if (!validateEmail(email) || password.length < 8) {
      return setErrorMessage("    .");
    }
    setErrorMessage("");
    login({ email, password });
  };

  return (
    <LoginContainer>
      <HeaderHelmet title=" | Discord" />
      <DefaultModal width={480}>
        <>
          <Text
            text="  !"
            color="white"
            fontSize="xxl"
            fontWeight="bold"
            center
            mb={8}
          />
          <AuthDesc text="   !" />
          {errorMessage !== "" && (
            <Text
              text={errorMessage}
              color="invite-danger"
              fontSize="xs"
              fontWeight="bold"
              mb={8}
            />
          )}
          <LoginForm
            text="  "
            value={email}
            type="email"
            onChange={changeEmail}
          />
          <LoginForm
            text=""
            value={password}
            type="password"
            onChange={changePassword}
          />
          <LinkTextContainer>
            <LinkText text=" ?" onClick={() => null} />
          </LinkTextContainer>
          <DefaultButton text="" onClick={onLogin} height={44} mb={12} />
          <Text
            text={
              <>
                <> ? </>
                <LinkText text="" onClick={onLoadRegister} />
              </>
            }
            color="auth-desc"
            fontSize="sm"
          />
        </>
      </DefaultModal>
    </LoginContainer>
  );
};

const LoginContainer = styled.div`
  background-color: ${({ theme }) => theme.backgroundColor.primary};
`;

const LinkTextContainer = styled.div`
  margin: -1rem 0 1.25rem;
`;

export default Login;
